import React from 'react';
import './UserTest.css';

function UserTest() {
    const user = {
        firstName: 'Harper',
        lastName: 'Perez'
    }

    function formatName(user) {
        return user.firstName + ' ' + user.lastName;
    }

    return <h1>Hello, {formatName(user)}</h1>;
}

export default UserTest;
