import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';

import TaniaApp from './tania/TaniaApp';
import Api from './Api';
import HelloTest from './test/HelloTest';
import UserTest from './test/UserTest';
import PropsTest from './test/PropsTest';
import Comment from './test/Comment';
import Clock from './test/Clock';
import ActionLink from './test/ActionLink';
import Toggle from './test/Toggle';
import Greeting from './test/Greeting';
import LoginControl from './test/LoginControl';
import Mailbox from './test/Mailbox';
import Page from './test/Page';
import NameForm from './test/NameForm';
import EssayForm from './test/EssayForm';
import FlavorForm from './test/FlavorForm';
import Reservation from './test/Reservation';
import Calculator from './test/statelift/Calculator';

import * as serviceWorker from './serviceWorker';

import './index.css';

//ReactDOM.render(<App />, document.getElementById('root'));
ReactDOM.render(<TaniaApp />, document.getElementById('tania-app'));
ReactDOM.render(<Api />, document.getElementById('tania-api'));

ReactDOM.render(<HelloTest />, document.getElementById('hello-test'));
ReactDOM.render(<UserTest />, document.getElementById('user-test'));
ReactDOM.render(<Clock />, document.getElementById('tick-test'));
ReactDOM.render(<PropsTest name="Sara"/>, document.getElementById('props-test'));

/* comment code */
const comment = {
    date: new Date(),
    text: 'I hope you enjoy learning React!',
    author: {
      name: 'Hello Kitty',
      avatarUrl: 'https://placekitten.com/g/64/64',
    },
  };
  ReactDOM.render(
    <Comment
      date={comment.date}
      text={comment.text}
      author={comment.author}
    />,
    document.getElementById('comment')
  );

  ReactDOM.render(<ActionLink />, document.getElementById('action-link'));
  ReactDOM.render(<Toggle />, document.getElementById('toggle-test'));
  ReactDOM.render(<Greeting isLoggedIn={ true }/>, document.getElementById('greeting'));
  ReactDOM.render(<LoginControl />, document.getElementById('logincontrol'));
  const messages = [];
  ReactDOM.render(<Mailbox unreadMessages={messages} />, document.getElementById('mailbox'));
  ReactDOM.render(<Page />, document.getElementById('page'));
  ReactDOM.render(<NameForm />, document.getElementById('name-form'));
  ReactDOM.render(<EssayForm />, document.getElementById('essay-form'));
  ReactDOM.render(<FlavorForm />, document.getElementById('flavor-form'));
  ReactDOM.render(<Reservation />, document.getElementById('reservation'));

  ReactDOM.render(<Calculator />, document.getElementById('temperature'));

// If you want your app to work offline and load faster, you can change
